import { Notification } from 'element-ui'

export function error (message) {
  Notification.error({
    title: '',
    message
  })
}

export function success (message) {
  Notification.success({
    title: '',
    duration: 2000,
    message
  })
}

export function notice (message) {
  Notification.warning({
    title: '',
    duration: 2000,
    message
  })
}
  


