import service from '@/api/all'
import { notice, success } from '@/api/index'
const server = service.articalApi
const state = {
}
const actions = {
  // 
  async articleCreate ({commit}, data) {
    const res = await server.articleCreate(data)
    if (res.code === '00') return success('')
    if (res && res.code === '02') {}
    else notice('')
  },
  // 
  async articleDel ({commit}, data) {
    const res = await server.articleDel(data)
    if (res && res.code === '00') return success('')
    if (res && res.code === '02') {}
    else notice('')
  },
  // 
  async articleGet ({commit}, data) {
    const res = await server.articleGet(data)
    if (res && res.code === '00') return res.data
    if (res && res.code === '02') {}
    else notice('')
  },
  // 
  async articleUpdate ({commit}, data) {
    const res = await server.articleUpdate(data)
    if (res && res.code === '00') return success('')
    if (res && res.code === '02') {}
    else notice('')
  }
}

const mutations = {
}

export default ({
  state,
  actions,
  mutations
})



