<template>
  <div class="manage-one">
    <!--  -->
    <div class="manage-add">
      <el-button type="primary" size="mini" @click="addNew"></el-button>
    </div>
    <div class="tabs">
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          prop="name"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="username"
          label=""
          width="180">
        </el-table-column>
        <!-- <el-table-column
          prop="department"
          label="">
        </el-table-column> -->
        <el-table-column
          prop="respons"
          label="">
        </el-table-column>
        <el-table-column label="" width="150">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="pageNum"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
      </el-pagination>
    </div>
    <!--  -->
    <el-dialog
      :title="isChange ? '' : ''"
      :visible.sync="addNewDialog"
      width="40%">
      <el-form :model="ruleForm" status-icon :rules="rules2" ref="ruleForm" label-width="110px" class="demo-ruleForm">
        <el-form-item label="" prop="name">
          <el-input type="text" v-model="ruleForm.name" placeholder="" :disabled="isChange"></el-input>
        </el-form-item>
        <!-- <el-form-item label="" prop="department">
          <el-input type="text" v-model="ruleForm.department" placeholder=""></el-input>
        </el-form-item> -->
        <el-form-item label="" prop="username">
          <el-input type="text" v-model="ruleForm.username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password" v-if="!isChange">
          <el-input type="text" v-model="ruleForm.password" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="repeatPassword" v-if="!isChange">
          <el-input type="text" v-model="ruleForm.repeatPassword" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="userid">
          <el-checkbox-group v-model="ruleForm.userid">
            <el-checkbox label=""></el-checkbox>
            <el-checkbox label=""></el-checkbox>
            <el-checkbox label=""></el-checkbox>
            <el-checkbox label=""></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')"> </el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="deleteDialog"
      width="30%">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteDialog = false"> </el-button>
        <el-button type="primary" @click="delSure"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'manage-one',
  data () {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== this.ruleForm.password) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    return {
      tableData: [],
      ruleForm: {
        name: '',
        // department: '',
        username: '',
        password: '',
        repeatPassword: '',
        userid: ['']
      },
      rules2: {
        name: [
          { required: true, trigger: 'blur', message: '' }
        ],
        // department: [
        //   { required: true, trigger: 'blur', message: '' }
        // ],
        username: [
          { required: true, trigger: 'blur', message: '' }
        ],
        password: [
          { required: true, trigger: 'blur', message: '' }
        ],
        repeatPassword: [
          { required: true, validator: validatePass, trigger: 'blur' }
        ],
        userid: [
          { required: true, type: 'array', trigger: 'change', message: '' }
        ]
      },
      addNewDialog: false,
      isChange: false,
      deleteDialog: false,
      currentPage: 0,
      pageNum: 10,
      totalCount: 0,
      keyword: '',
      infoId: ''
    }
  },
  methods: {
    handleEdit (index, row) {
      this.infoId = row.id
      this.isChange = true
      this.addNewDialog = true
      this.ruleForm.name = row.name
      this.ruleForm.password = row.password
      // this.ruleForm.department = row.department
      this.ruleForm.username = row.username
      let arr = row.userid.split(',')
      arr.forEach((item, i) => {
        if (Number(item) === 1) {
          arr[i] = ''
        } else if (Number(item) === 2) {
          arr[i] = ''
        } else if (Number(item) === 3) {
          arr[i] = ''
        } else if (Number(item) === 4) {
          arr[i] = ''
        }
      })
      this.ruleForm.userid = arr
    },
    handleDelete (index, row) {
      this.infoId = row.id
      this.deleteDialog = true
    },
    addNew () {
      this.isChange = false
      this.addNewDialog = true
      this.ruleForm = {
        name: '',
        // department: '',
        username: '',
        password: '',
        repeatPassword: '',
        userid: []
      }
    },
    submitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          let arr = []
          this.ruleForm.userid.forEach(item => {
            if (item === '') {
              arr.push(1)
            } else if (item === '') {
              arr.push(2)
            } else if (item === '') {
              arr.push(3)
            } else if (item === '') {
              arr.push(4)
            }
          })
          let userids = arr.join(',')
          if (this.isChange) {
            await this.$store.dispatch('userChange', {
              // name: this.ruleForm.name,
              // department: this.ruleForm.department,
              username: this.ruleForm.username,
              password: this.ruleForm.password,
              userid: userids,
              id: this.infoId
            })
          } else {
            await this.$store.dispatch('userAdd', {
              name: this.ruleForm.name,
              // department: this.ruleForm.department,
              username: this.ruleForm.username,
              password: this.ruleForm.password,
              userid: userids
            })
          }
          this.getList()
          this.$refs[formName].resetFields()
        } else {
          return false
        }
        this.addNewDialog = false
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.addNewDialog = false
    },
    handleSizeChange (val) {
      this.pageNum = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getList()
    },
    async getList () {
      await this.$store.dispatch('userGet', {
        pageNum: this.currentPage,
        pageSize: this.pageNum,
        username: this.keyword
      }).then((res) => {
        this.tableData = []
        if (res && res.list && res.list.length > 0) {
          this.tableData = res.list.map((item, i) => ({
            ...item,
            respons: this.filArr(item.userid)
          }))
        }
      })
    },
    async delSure () {
      this.deleteDialog = false
      await this.$store.dispatch('userDel', {
        groupId: this.infoId
      })
      this.getList()
    },
    filArr (item) {
      if (item) {
        let arr = item.split(',')
        arr.forEach((items, i) => {
          if (Number(items) === 1) {
            arr[i] = ''
          } else if (Number(items) === 2) {
            arr[i] = ''
          } else if (Number(items) === 3) {
            arr[i] = ''
          } else if (Number(items) === 4) {
            arr[i] = ''
          }
        })
        let arrs = arr.join('')
        return arrs
      } else return []
    }
  },
  created () {
    this.getList()
  }
}
</script>

<style lang="scss">
.manage-one {
  text-align: left;
  .manage-add {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #efefef;
    padding-left: 20px;
    box-sizing: border-box;
  }
  .tabs {
    padding: 20px;
    .el-table th:first-child {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
    th {
      background: #f5f7fa;
    }
  }
  .el-form {
    .el-input {
      width: 90%;
    }
  }
  .el-pagination {
    margin-top: 20px;
    text-align: right;
  }
}
</style>



