import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/home'
import index from '@/pages/index/index'
import news from '@/pages/news/index'
import newsDetail from '@/pages/news/detail'
import notice from '@/pages/notice/index'
import noticeDetail from '@/pages/notice/detail'
import productInfo from '@/pages/productInfo/index'
import productDetail from '@/pages/productInfo/detail'
import partyBuild from '@/pages/partyBuild/index'
import partyBuildDetail from '@/pages/partyBuild/detail'
import download from '@/pages/download/index'
import manage from '@/pages/manage/index'
import manageAccount  from '@/pages/manage/childrens/one'
import manageArticle  from '@/pages/manage/childrens/two'
import manageFile  from '@/pages/manage/childrens/three'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home,
      redirect: '/index',
      children: [
        { path: '/index', component: index, name: '', meta: { page: 0 } },
        { path: '/news', component: news, name: '', meta: { page: 1 } },
        { path: '/newsDetail', component: newsDetail, name: '', meta: { page: 1 } },
        { path: '/notice', component: notice, name: '', meta: { page: 2 } },
        { path: '/noticeDetail', component: noticeDetail, name: '', meta: { page: 2 } },
        { path: '/productInfo', component: productInfo, name: '', meta: { page: 3 } },
        { path: '/productDetail', component: productDetail, name: '', meta: { page: 3 } },
        { path: '/partyBuild', component: partyBuild, name: '', meta: { page: 4 } },
        { path: '/partyBuildDetail', component: partyBuildDetail, name: '', meta: { page: 4 } },
        { path: '/download', component: download, name: '', meta: { page: 5 } },
        {
          path: '/manage',
          component: manage,
          name: '',
          redirect: '/manage/manageAccount',
          meta: { page: 6 },
          children: [
            { path: '/manage/manageAccount', component: manageAccount, name: '', meta: { page: 6, small: 1 } },
            { path: '/manage/manageArticle', component: manageArticle, name: '', meta: { page: 6, small: 2 } },
            { path: '/manage/manageFile', component: manageFile, name: '', meta: { page: 6, small: 3 } },
          ]
        },
      ]
    }
  ]
})



