import axios from './axios'

// 
export function articleCreate (data) {
  return axios.post('/article/create', data).then((res) => res.data)
}
// 
export function articleDel (data) {
  return axios.delete('/article/delete', { params: data }).then((res) => res.data)
}
// 
export function articleGet (data) {
  return axios.get('/article/select', { params: data }).then((res) => res.data)
}
// 
export function articleUpdate (data) {
  return axios.post('/article/update', data).then((res) => res.data)
}


